import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios';
import VueAxios from 'vue-axios';
import Home from './views/Home.vue';
import News from './views/News.vue';
import Nvid from './views/nvid.vue';
import Nvid2 from './views/nvid2.vue';
import Nvid0 from './views/nvid0.vue';
import Book0 from './views/book0.vue';
import Wait from './views/wait.vue';
import WaitTwo from './views/waittwo.vue';
Vue.use(Router)
Vue.use(VueAxios,axios)

export default new Router({
  routes: [
    {
      path:'/',
      component:Home,
      children:[
        {
          path:'/News/:name',
          props:true,
          name:'News',
          component:News,
          meta: {
            index: 0
          }
        },
        {
          path:'/',
          props:true,
          name:'Nvid0',
          component:Nvid0,
          meta: {
            index: 1
          }
        },
        {
          path:'/Book0/:name',
          props:true,
          name:'Book0',
          component:Book0,
          meta: {
            index: 2
          }
        },
        {
          path:'/Wait/:name',
          props:true,
          name:'Wait',
          component:Wait,
          meta: {
            index: 3
          }
        }
      ]
    },
   
    {
      path:'/Nvied/:vid',
      props:true,
      name:'Nvid',
      component:Nvid
    },
    {
      path:'/Nvied2/:vid',
      props:true,
      name:'Nvid2',
      component:Nvid2
    },
    {
      path:'/WaitTwo/:name',
      props:true,
      name:'WaitTwo',
      component:WaitTwo,
    }
  ]
})
