<template>
    <div 
    @touchstart="touchStart"
    @touchmove="touchMove"
    @touchend="touchEnd">
        <app-header1 v-bind:style="{marginTop:top+'vw'}"></app-header1>
        <tap @changed="change" :tabs="tabs" :currentTab="currentTab"></tap>
        <transition name="fade">
            <router-view v-bind:style="{height:heig-24.4+'vw'}"></router-view>
        </transition>
        <Foot1></Foot1>
    </div>
    
</template>

<script>
    import appHeader1 from '../components/Header1.vue';
    import tap from '../components/tap.vue';
    import Foot1 from '../components/foot.vue';
    export default {
        components:{
            appHeader1,
            Foot1,
            tap,
        },
        data:function(){
            return{
                top:0,
                toptwo:0,
                xvideos:[],
                heig:0,
                length:0,
                i:1,
                tabs: [
                    {'id': 't1', 'label': '直播'},
                    {'id': 't2', 'label': '推荐'},
                    {'id': 't3', 'label': '热门'},
                    {'id': 't4', 'label': '追番'},
                    {'id': 't5', 'label': '影视'},
                    {'id': 't6', 'label': '70年'},
                ],
                currentTab:'t2',
            };
        },
        created () {
            this.touch = {}
        },
        methods:{
            //获取触摸位置
            touchStart(e) {
                // window.console.log("move",this.top)
                const touch = e.touches[0]
                this.touch.startX = touch.pageX
                this.touch.startY = touch.pageY
            },
            //判定触摸方向
            touchMove(e) {
                var that=this
                const touch = e.touches[0]
                const deltaX = touch.pageX - this.touch.startX
                const deltaY = touch.pageY - this.touch.startY
                this.touch.deltaX = deltaX
                this.touch.deltaY = deltaY
                this.touch.pageY = touch.pageY
                if (Math.abs(deltaY) > Math.abs(deltaX)) {
                    this.top=this.top+Math.round(100*deltaY/window.innerHeight);
                    if(this.top>=0){
                        that.top = 0;
                    }
                    if(this.top<-15){
                        that.top =-15;
                    }
                }
            },
            //结束触摸
            touchEnd() {
                var that=this
                window.console.log("this.touchXX",this.touch)
                if (Math.abs(that.touch.deltaY) < Math.abs(that.touch.deltaX)) {
                    if(that.touch.pageY>280){
                        window.console.log("1",this.touch)
                        if(that.touch.deltaX<-100){
                            window.console.log("2")
                            that.i = that.i+1;
                            if(that.i>that.length){
                                that.i=0;
                            }
                            this.change (this.tabs[that.i].id)
                        }else if(that.touch.deltaX>100){
                            that.i =that.i-1;
                            if(that.i<0){
                                that.i=that.length
                            }
                            this.change (this.tabs[that.i].id)
                        }   
                        window.console.log(that.i,that.length);                   
                    }
                    window.console.log("3",that.i,that.length)
                }
                if(this.top>=0){
                    this.top = 0;
                }
                if(this.top<0){
                    this.top =-15;
                }

            },
            heigh(){
                this.heig = Math.round(100*window.innerHeight/window.innerWidth)
            },
            change (data) {
                //这里写切换tab之后你需要做的事，data为切换后的tabId
                if(data == this.tabs[0].id){
                    this.currentTab=this.tabs[0].id;
                    this.$router.push({
                    path: '/News/:name',
                    })
                }
                if(data == this.tabs[1].id){
                    this.currentTab=this.tabs[1].id;
                    this.$router.push({
                    path: '/',
                    })
                }
                if(data == this.tabs[2].id){
                    this.currentTab=this.tabs[2].id;
                    this.$router.push({
                    path: '/Book0/:name',
                    })
                }
                if(data == this.tabs[3].id){
                    this.currentTab=this.tabs[3].id;
                    this.$router.push({
                    path: '/Wait/:name',
                    })
                }
                if(data == this.tabs[4].id){
                    this.currentTab=this.tabs[4].id;
                    this.$router.push({
                    path: '/Wait/:name',
                    })
                }
                if(data == this.tabs[5].id){
                    this.currentTab=this.tabs[5].id;
                    this.$router.push({
                    path: '/Wait/:name',
                    })
                }
            },	
        },
        mounted(){
            this.heigh();
            this.length=this.tabs.length-1;
        },
    }
</script>

<style scoped>
.fade-enter {
  opacity:0;
}
.fade-leave{
  opacity:1;
}
.fade-enter-active{
  transition:opacity .5s;
}
.fade-leave-active{
  opacity:0;
  transition:opacity .5s;
}
</style>