<template>
    <!-- 轮播图组件-->
    <div class="slidebigbox" @touchstart.prevent="touchStart"
  @touchmove.prevent="touchMove"
  @touchend="touchEnd">
        <div v-for="(img,index) in dataimg" 
            v-bind:class="transition==1?'box11':''" :key="'info-'+ index"
            v-bind:style="{ left: width*(index-mark)+movelin + 'vw' }" 
            class="box1">
            <img :src="img.src" alt="Image">
        </div>
        <div v-for="(img,index) in dataimg" 
                v-bind:class="index==mark?'index11':''" 
                v-bind:style="{ right: 12.8-index*(2.5) + 'vw' }"
                v-show="index!=0&&index!=5" 
                class="indx" :key="'info1-'+ index">
        </div>
    </div>
</template>
<script>
export default {
    props: ['name'],
    data:function(){
        return{
            xvideos:[],
            timer:null,
            movelin:0,
            mark:1,
            width:93.8,
            transition:1,
            dataimg: [ 
                {
                    src:'http://112.74.160.130:8080/seed/images/four.jpg',
                },{
                    src:'http://112.74.160.130:8080/seed/images/one.jpg',
                },
                {
                    src: 'http://112.74.160.130:8080/seed/images/two.jpg',
                },
                {
                    src:'http://112.74.160.130:8080/seed/images/three.jpg',
                },
                {
                    src:'http://112.74.160.130:8080/seed/images/four.jpg',
                },{
                    src:'http://112.74.160.130:8080/seed/images/one.jpg',
                }
            ],
        };
        
    },
    created () {
        this.play();
        this.touch = {}
    },
    methods: {
        //获取触摸位置
        touchStart(e) {
            this.stop();    //停止定时器
            const touch = e.touches[0]
            this.touch.startX = touch.pageX
            this.touch.startY = touch.pageY
        },
        //判定触摸方向 轮播位移程度
        touchMove(e) {
            const touch = e.touches[0]
            const deltaX = touch.pageX - this.touch.startX
            const deltaY = touch.pageY - this.touch.startY
            if (Math.abs(deltaY) > Math.abs(deltaX)) {
                return
            }
            this.movelin=Math.round(this.width*deltaX/window.innerWidth)
            window.console.log("move",this.mark)
        },
        //结束触摸
        touchEnd() {
            if(this.movelin>(this.width/2)){
                this.transition=1;
                this.mark=this.mark-1;
                if(this.mark<=0){
                    setTimeout(this.markMothertwo,300)
                }
            }else if(this.movelin<(-this.width/2)){
                this.transition=1;
                this.mark=this.mark+1;
                if(this.mark>=5){
                    setTimeout(this.markMother,300)
                }
            }
            this.movelin=0;  
            this.play();    //开始定时器         
        },
        //定时器开始
        play () {
            this.timer = setInterval(this.autoPlay, 3500)
        },
        //定时器停止
        stop () {
            clearInterval(this.timer)
        },
        //定时器执行函数
        autoPlay () {
            var that = this
            that.transition=1
            that.mark++;
            if (that.mark >= 5) {
                setTimeout(that.markMother,300)
            }
        },
        //延迟执行函数 用于实现无缝轮播
        markMother(){
            this.transition=0;
            this.mark=1;
        },
        //延迟执行函数 用于实现无缝轮播
        markMothertwo(){
            this.transition=0;
            this.mark=4;
        },
    },
}
</script>
<style lang="less">
    .slidebigbox{
        position: relative;
        margin-top: 2.8vw;
        width: 93.8vw;
        height: 27.8vw;
        margin-left: 3.1vw;
        border-radius: 1vw;
        background-color: rgba(100,100,100,.1);
        z-index:1;
        overflow: hidden;
        .box1{
            position: absolute;
            top: 0;
            width: 93.8vw;
            height: 27.8vw;
            img{
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                margin: 0;
                width: 93.8vw;
                max-height: 50vw;
                background-size: 100% auto;
                background-position: 50% 50%;
            }
        }
        .box11{
            transition: all .3s;
        }
        .indx{
            position: absolute;
            top: 23.9vw;
            height: 1.5vw;
            width: 1.5vw;
            box-sizing: border-box; 
            border-radius: 50%;
            background-color: rgba(255,255,255,1);
        }
        .index11{
            background-color: #FE6F8E;
        }
    }
</style>
