<template>
    <div class="bigbox">
        <!-- 自制轮播图组件 -->
        <slideshow></slideshow>
        <!-- 视频列表 -->
        <div class="box" v-for="xvideo in xvideos" :key="xvideo.id" @click="go(xvideo)">
            <div class="vpic">
                <img :src="xvideo.data.content.data.cover.detail" alt="Image">
            </div>
            <div class="title"> {{xvideo.data.content.data.title}}</div>
            <div class="type">
                <span class="type1"> {{xvideo.data.content.data.category}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    import slideshow from '../components/slideshow.vue';
    export default {
        components:{
            slideshow,
        },
        data:function(){
            return{
                xvideos:[],
            };
        },
        methods:{
            getData(){
                // this.axios.get('https://api.apiopen.top/todayVideo')
                this.axios.get('./News.json')
                .then((response)=>{
                    window.console.log(response.data.result)
                    this.xvideos=response.data.result;})
            },
            go(xvideo){
                // this.vid = xvideo.data.content.data.playUrl;
                this.$router.push({
                    name:'Nvid',
                    params:{
                        vid:xvideo.data.content.data.playUrl,
                        tit:xvideo.data.content.data.description,
                        ava:xvideo.data.header.icon,
                        nam:xvideo.data.header.name,
                        pic:xvideo.data.content.data.cover.detail
                    }
                })
            },
        },
        mounted(){
            this.getData();
        }
    }
</script>

<style lang="less">
.bigbox{
    position: relative;
    width: 100vw;
    box-sizing: border-box;
    overflow-y:scroll;
    background-color: rgb(244,244,244);
    padding-bottom: 5vw;
    .box{
        position: relative;
        box-sizing: border-box;
        margin-left: 3.1vw;
        margin-top: 2vh;
        width: 45.4vw;
        height: 48.6vw;
        float: left;
        overflow: hidden;
        border-radius: 5px;
        background-color: rgba(255,255,255,1);
        box-shadow: 0px 2px 5px -2px rgb(150,150,150,.1);
        .vpic{
            border-radius: 5px;
            width: 100%;
            height: 28.3vw;
            img{
                border-radius: 5px 5px 0 0;
                width: 100%;
                height: 100%;
            }
        }
        .title{
            position: relative;
            width: 100%;
            padding: 2.3vw 3.6vw;
            font-weight: 400;
            box-sizing: border-box;
            height: 14.2vw;
            line-height: 4.6vw;
            font-size: 3.7vw;
            font-weight: 500;
            color: #232625;
            text-align: justify;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
        .type{
            position: relative;
            width: 100%;
            font-weight: 200;
            padding: 0vw 3.6vw;
            box-sizing: border-box;
            height: 5.5vw;
            text-align: justify;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            border-radius:0px 0px 5px 5px;
            .type1{
                position: relative;
                display:inline-block;
                box-sizing: border-box;
                width: auto;
                height: 4.2vw;
                line-height: 4vw;
                padding:0vw 1vw;
                border-radius: .9vw;
                font-size: 2.4vw;
                border: .3vw solid #CE688D;
                color: #CE688D;
            }
        }
    }
}
</style>