<template>
  <!-- 导航栏 -->
  <div class="tab_box">
    <!--这里是tab的每一项-->
    <div v-for="item in tabs" :key="'tabItem' + item.id" :class="['tab_item', {'active_tab':currentTab === item.id}]" @click="changeTab(item.id)">
      {{item.label}}
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // currentTab: '' // 用于记录tab当前所在项的id
    }
  },
  props: ['tabs','currentTab'], // 需要父组件传入
  methods: {
    changeTab (id) {
      // this.currentTab = id
      this.$emit('changed', id) // 点击tab时触发changed方法，由父组件处理
    }
  },
  // mounted: function () {
  //   this.currentTab = this.tabs[1].id // 默认下划线在第2项
  // }
}
</script>
<style lang="less">
.tab_box{
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content:space-around;
  margin: 0 auto;
  width:100%;
  height: 11.1vw;
  background-color: rgb(255,255,255);
  border-bottom: 1px solid rgba(100,100,100,.1);
  .tab_item{
    box-sizing: border-box;
    height: 11.1vw;
    line-height: 10.1vw;
    font-size: 3.6vw;
    font-weight: 500;
    padding: .5vw;
    color: #989994;
    transition: all .4s;
    border-bottom: 0px solid #fff; 
    /*如果想修改tab内的字体颜色和tab样式，在这里修改*/
  }
  .active_tab {
    /*这是下划线样式，可以根据需要修改*/
    color: #FA7298;
    border-bottom: 2px solid #FA7298; 
  }
}
</style>