<template>
    <!-- 播放视频页面 -->
    <div class="vid">
        <div  class="back" @click="back()"></div>
        <!-- 自制播放器组件 -->
        <voide :vid='vid'></voide>
        <div class="vbody" v-bind:style="{height:heig-56.7+'vw'}"
        @touchstart="touchStartVideo"
        @touchmove="touchMoveVideo"
        @touchend="touchEndVideo"
        >
            <!-- 导航栏 -->
            <div class="vid2">
                <div class="left">
                   <tap @changed="change" :tabs="tabs" :currentTab="currentTab"></tap>
                </div>
                <div class="right">
                    <div class="box21" :class="{show: !ok}">
                        <div class="btn1" v-show="ok">{{bar}}</div>
                        <div class="btn2" @click="Ok()">弹
                            <div v-if="ok" class="pic1"></div>
                            <div v-else class="pic2"></div>
                        </div>
                    </div>
                </div>
            </div>
            
            <div class="vid3" v-bind:style="{height:heig-67.5+'vw'}">
                <div class="infomation">
                    <!-- 简介 -->
                    <div class="box11">
                        <div class="ava">
                            <img :src="ava" alt="">
                            <div class="name">
                                <div class="txt1">{{nam}}</div>
                                <div class="txt2">62粉丝</div>
                            </div>
                            <div class="attention">
                                + 关注
                            </div>
                        </div>
                        <div class="title" :class="{titleactive: Tle}">
                            <div class="btn" :class="Tle ? 'btn1':'btn2'" @click="Title()"></div>
                            {{tit}}
                        </div>
                        <div class="info">
                            <div class="box0">
                                <div class="pic"></div>
                                43.6万
                            </div>
                            <div class="box2">
                                <div class="pic"></div>
                                287
                            </div>
                            <div class="box3">7-26</div>
                            <div class="box4">Av60861796</div>
                        </div>
                    </div>
                    <!-- 视频播放列表 -->
                    <div class="box12" v-for="xvideo in xvideos" :key="xvideo.id" @click="go(xvideo)">
                        <div class="left">
                            <img :src="xvideo.data.content.data.cover.detail" alt="">
                        </div>
                        <div class="right">
                            <div class="title">{{xvideo.data.content.data.title}}</div>
                            <div class="name1">
                                <!-- <img :src="xvideo.data.header.icon" alt=""> -->
                                <div class="pic"></div>
                                <div class="mingcheng">{{xvideo.data.header.name}}</div>
                            </div>
                            <div class="name2">
                                <div class="box0">
                                    <div class="pic"></div>
                                    43.6万
                                </div>
                                <div class="box2">
                                    <div class="pic"></div>
                                    287
                                </div>
                            </div>
                        </div>
                    </div>
