     </div>
</template>

<script>
    import voide from "../components/voide.vue";
    import tap from '../components/tap.vue';
    export default {
        components:{
            voide,
            tap,
        },
        props:['vid','tit','ava','nam','pic'],
        data:function(){
            return{
                xvideos:[],
                tabs: [
                    {'id': 't1', 'label': '简介'},
                    {'id': 't2', 'label': '评价'},
                ],
                currentTab:'t1',
                bar:'点我发弹幕',
                ok:true,
                Tle:true,
                heig:0,
            };
        },
        created () {
            this.touch = {}
        },
        methods:{
            getData(){
                this.axios.get('./News.json')
                .then((response)=>{
                    this.xvideos=response.data.result;
                    window.console.log(this.xvideos)
                })
            },
            change (data) {
                //这里写切换tab之后你需要做的事，data为切换后的tabId
                // window.console.log(data);
                if(data == this.tabs[0].id){
                    this.currentTab=this.tabs[0].id;                  
                }
                if(data == this.tabs[1].id){
                    this.currentTab=this.tabs[1].id;
                }
            },
            //下一个路由
            go(xvideo){
                // this.vid = xvideo.data.content.data.playUrl;
                this.$router.push({
                    name:'Nvid2',
                    params:{
                        vid:xvideo.data.content.data.playUrl,
                        tit:xvideo.data.content.data.description,
                        ava:xvideo.data.header.icon,
                        nam:xvideo.data.header.name,
                        pic:xvideo.data.content.data.cover.detail
                    }
                })
            },
            //幽灵按钮
            Ok(){
                this.ok=!this.ok;
            },
            //简介缩放
            Title(){
                this.Tle=!this.Tle;
            },
            //获取页面高度
            heigh(){
                this.heig = Math.round(100*window.innerHeight/window.innerWidth)
            },
            /**
             * 返回上一个路由
             */
            back(){
                this.$router.back(-1);
            },
            //获取触摸位置
            touchStartVideo(e) {
                const touch = e.touches[0]
                this.touch.startX = touch.pageX
                this.touch.startY = touch.pageY
            },
            //判定触摸方向
            touchMoveVideo(e) {
                const touch = e.touches[0]
                const deltaX = touch.pageX - this.touch.startX
                const deltaY = touch.pageY - this.touch.startY
                this.touch.deltaX = deltaX
                this.touch.deltaY = deltaY
                this.touch.pageY = touch.pageY
                if (Math.abs(deltaY) > Math.abs(deltaX)) {
                   return;
                }
            },
            //结束触摸
            touchEndVideo() {
                var that=this
                if (Math.abs(that.touch.deltaY) < Math.abs(that.touch.deltaX)) {
                    if(that.touch.deltaX>0){
                        that.back();
                    }
                }
            },
        },
        mounted(){
            this.heigh();
            this.getData();
        }

  }
</script>

