<template>
    <div class="bookbigbox" v-bind:style="{height:heig-24.4+'vw'}">
        <div class="box1" v-for="book in books" :key="book.id">
            <div class="left">
                <img :src="book.book_cover" alt="" @click="go()">
            </div>
           
            <div class="right">
                <div class="title">《{{book.bookname}}》</div>
                <div class="author">作者：{{book.author_name}}</div>
                <div class="conent"> {{book.introduction}}</div>
                 <div class="type">{{book.stat_name}} . {{book.class_name}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
         props:[],
         data:function(){
            return{
                heig:0,
                books:[]
            };
            
        },
        methods:{
            heigh(){
                this.heig = Math.round(100*window.innerHeight/window.innerWidth)
            },
            getData(){
                this.axios.get('https://www.apiopen.top/novelApi')
                .then((response)=>{this.books=response.data.data; window.console.log(this.books)})
               
            },
            go(){
                // this.vid = xvideo.data.content.data.playUrl;
                this.$router.push({
                    name:'WaitTwo',
                })
            },
        },
        mounted(){
            this.getData();
        }

    }
</script>
