<template>
    <div :class="full ? 'video-player1':'video-player'" ref="vcontainer">
        <!-- 播放容器 -->
        <video class="vid21" ref="v" 
        poster="../../public/img/video/wait.svg"
        @ended="handleEnd" 
        @timeupdate="handleTimeUpdate"
        @click="bigbarLook">
            <source :src="vid" type="video/mp4">
            <source :src="vid" type="video/ogg">
        </video>
        <!-- 控制条 -->
        <div v-bind:class="booleanbigbar?'controller1':''" class="controller">
            <!-- 开始 暂停 -->
            <div class="play" @click="togglePlaying">
                <div class="pic stop" v-if="isPaused"></div>
                <div class="pic again" v-else></div>
            </div>
            <!-- 进度条 （小屏）-->
            <div ref="p" v-if="!full" class="bigbar"
            @touchstart.prevent="touchStart"
            @touchmove.prevent="touchMove"
            @touchend="touchEnd"
            >
                <div class="box1"></div>
                <div class="smallbar" v-bind:style="{width: width+'px'}">
                    <div class="pic"></div>
                </div>
                
            </div>
            <!-- 进度条 （大屏） -->
            <div ref="p" v-else class="bigbar"
            @touchstart.prevent="touchStart1"
            @touchmove.prevent="touchMove1"
            @touchend="touchEnd1"
            >
                <div class="box1"></div>
                <div class="smallbar" v-bind:style="{width: width1+'px'}">
                    <div class="pic"></div>
                </div>
                
            </div>
            <div class="time">{{videoTime}}</div>
            <div class="screen" @click="toggleFullscreen"></div>
            <!-- <button @click="stopPlaying">重播</button> -->
            <!-- <button @click="toggleMute">静音</button> -->
        </div>

    </div>
</template>
<script>
/**
 * 将时间转换为分秒的方法
 * originalSec 视频时长
 */
function secToTimer(originalSec) {
    const min = Math.floor(originalSec / 60);
    const sec = Math.floor(originalSec % 60);
    const minStr = min < 10 ? `0${min}` : String(min);
    const secStr = sec < 10 ? `0${sec}` : String(sec);
    return `${minStr}:${secStr}`;
}
export default {
    props:['vid'],
    data() {
        return {
            video: null,    //视频容器
            progress: null, //拖动条
            isPaused: true, //视频是否暂停
            videoTime: '00:00/00:00',
            videoProgress:0,
            width:0,
            width1:0,
            progressPercent:0,      //进度条百分比
            booleanProgress:true,   //判断进度条是否随着 视频播放 而变化
            booleanbigbar:true,    //控制条不可见
            full:false,
        }
    },
    created () {
        this.touch = {}
    },
    methods: {             
        /**--------------------按钮事件begin------------------ */
        //播放暂停事件
        togglePlaying() {
            if (this.video.paused) {
                this.playVideo();             //调用播放事件
            } else {
                this.pauseVideo();            //调用暂停事件
            }
        },
        //当视频播放时会发生的事件
        handleTimeUpdate() {
            const progressRect = this.progress.getBoundingClientRect();
            this.videoTime = this.refreshTime();                                        //调用当前播放时间事件
            if(this.booleanProgress){                                                   //当 拖动进度条时 暂停
                this.booleanProgress = this.video.currentTime / this.video.duration;    //计算 当前播放时间 与 总时间的比 
                this.width = progressRect.width*this.booleanProgress                    //计算进度条的宽度
                this.width1 = progressRect.height*this.booleanProgress   
            }
        },
        //全屏事件 h5 fullscreen 全屏模式
        toggleFullscreen() {
            const isFullscreen = document.webkitIsFullScreen || document.fullscreen;
            //判断当前是否有元素在全拼模式
            if (isFullscreen) {
                //exitFullscreen() 退出全屏模式。
                this.full = false;
                const exitFunc = document.exitFullscreen || document.webkitExitFullscreen;
                exitFunc.call(document);
            } else {
                const element = this.$refs.vcontainer;
                //requestFullscreen() 请求进入全屏模式。
                this.full = true;
                const fullscreenFunc = element.requestFullscreen || element.webkitRequestFullScreen;
                //将 视频Dom元素进入全屏模式
                fullscreenFunc.call(element);
            }
        },
        bigbarLook(){
            this.booleanbigbar=!this.booleanbigbar;
            setTimeout(this.bigbarLookTwo,5000);
        },
        //一定时间后自动隐藏 进度条
        bigbarLookTwo(){
            this.booleanbigbar=true;
        },
/**-------------------------------------进度条事件begin----------------------------------------------------------------------- */
        //获取触摸位置（小屏）
        touchStart(e) {
            this.booleanProgress = false //进度条不随着时间变化而变化
            const touch = e.touches[0]
            this.touch.startX = touch.pageX
            this.touch.startY = touch.pageY
            
            const progressRect = this.progress.getBoundingClientRect();
            this.progressPercent = (touch.pageX - progressRect.left) / progressRect.width;
            if (this.progressPercent < 0) {
                this.progressPercent = 0;
            } else if (this.progressPercent > 1) {
                this.progressPercent = 1;
            }
            this.width=touch.pageX - progressRect.left;
            // this.video.currentTime = this.video.duration * this.progressPercent;
        },
        //判定手指在进度条上的滑动（小屏）
        touchMove(e) {
            const progressRect = this.progress.getBoundingClientRect();
            const touch = e.touches[0]
            window.console.log('3',touch.pageX)
            this.progressPercent = (touch.pageX - progressRect.left) / progressRect.width;
            if (this.progressPercent < 0) {
                this.progressPercent = 0;
            } else if (this.progressPercent > 1) {
                this.progressPercent = 1;
            }
            if((touch.pageX - progressRect.left)>progressRect.width){
                this.width=progressRect.width
            }else{
                this.width=touch.pageX - progressRect.left;
            }
            this.booleanbigbar=false;
        },
        //结束触摸（小屏）
        touchEnd() {
            // this.playVideo()   
            this.video.currentTime = this.video.duration *this.progressPercent;
            this.booleanProgress = true
            setTimeout(this.bigbarLookTwo,5000);
        },

        //获取触摸位置（大屏）
        touchStart1(e) {
            this.booleanProgress = false //进度条不随着时间变化而变化
            const touch = e.touches[0]
            this.touch.startX = touch.pageX
            this.touch.startY = touch.pageY
            
            const progressRect = this.progress.getBoundingClientRect();
            this.progressPercent = (touch.pageY - progressRect.top) / progressRect.height;
            if (this.progressPercent < 0) {
                this.progressPercent = 0;
            } else if (this.progressPercent > 1) {
                this.progressPercent = 1;
            }
            this.width1=touch.pageY - progressRect.top;
            // this.video.currentTime = this.video.duration * this.progressPercent;
        },
        //判定手指在进度条上的滑动（大屏）
        touchMove1(e) {
            const progressRect = this.progress.getBoundingClientRect();
            const touch = e.touches[0]
            window.console.log('3',touch.pageY, progressRect.height)
            this.progressPercent = (touch.pageY - progressRect.top) / progressRect.height;
            if (this.progressPercent < 0) {
                this.progressPercent = 0;
            } else if (this.progressPercent > 1) {
                this.progressPercent = 1;
            }
            if((touch.pageY - progressRect.top)>progressRect.height){
                this.width1=progressRect.height
            }else{
                this.width1=touch.pageY - progressRect.top;
            }
            this.booleanbigbar=false;
        },
        //结束触摸（大屏）
        touchEnd1() {
            // this.playVideo()   
            this.video.currentTime = this.video.duration *this.progressPercent;
            this.booleanProgress = true
            setTimeout(this.bigbarLookTwo,5000);
        },
/**-----------------------------------公共事件------------------------------------------------ */
        //播放视频
        playVideo() {
            this.isPaused = false;
            this.video.play();
        },
        //暂停视频
        pauseVideo() {
            this.isPaused = true;
            this.video.pause();
        },
        //视频播放完成手动更新isPaused状态
        handleEnd() {
            this.pauseVideo();
        },
        //暂停 并 重新开始
        stopPlaying() {
            this.video.currentTime = 0;
            this.pauseVideo();
        },
        //静音
        toggleMute() {
            this.video.muted = !this.video.muted;
            this.isMuted = this.video.muted;
        },
        //当前播放时间事件
        refreshTime() {
            if (!this.video) {
                return `${secToTimer(0)}/${secToTimer(0)}`;
            }
            const currTime = this.video.currentTime || 0;
            const duration = this.video.duration || 0;
            return `${secToTimer(currTime)}/${secToTimer(duration)}`;
        },
    },
    computed: {
        // videothis.progressPercent() {
        //     return `${this.videoProgress * 100}%`;
        // },
    },
    mounted() {
        //  访问 video 元素
        this.video = this.$refs.v;
        //  访问 进度条 元素
        this.progress = this.$refs.p;
        this.togglePlaying()
    },
}
</script>
<style lang="less">

