            }
            .smallbar{
                position: absolute;
                z-index: 1;
                top: 3vw;
                float: left;
                // width: 10vw;
                height: .9vw;
                background-color: #FB739C;
                .pic{
                    position: relative;
                    margin-top: -1.9vw;
