import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Mui from 'vue-awesome-mui'
import './plugins/element.js'
import plus from 'vue-h5-plus'
Vue.use(plus)
Vue.use(Mui)
Vue.config.productionTip = false

new Vue({
  router,
  Mui,
  render: h => h(App)
}).$mount('#app')
