<template>
    <div class="bookbigbox">
        <div class="box1" v-for="nnew in news" :key="nnew.ptime">
            <a style="-webkit-tap-highlight-color: rgba(255, 255, 255, 0);
                       -webkit-user-select: none;
                       -moz-user-focus: none;
                       -moz-user-select: none;" :href="nnew.link">
                <div class="left" v-for="pic in nnew.picInfo" :key="pic.id">
                    <img :src="pic.url" alt="">
                </div>
            
            <div class="right">
                <div class="title">{{nnew.title}}</div>
                <div class="author">{{nnew.source}}</div>
                <div class="conent">{{nnew.title}} </div>
                 <div class="type">{{nnew.category}}.{{nnew.ptime}}</div>
            </div>
            </a>
        </div>
        <div class="box1" v-for="neew in nnews" :key="neew.ptime">
            <a style="-webkit-tap-highlight-color: rgba(255, 255, 255, 0);
                       -webkit-user-select: none;
                       -moz-user-focus: none;
                       -moz-user-select: none;" :href="neew.link">
                <div class="left" v-for="pic in neew.picInfo" :key="pic.id">
                    <img :src="pic.url" alt="">
                </div>
            
            <div class="right">
                <div class="title">{{neew.title}}</div>
                <div class="author">{{neew.source}}</div>
                <div class="conent">{{neew.title}} </div>
                 <div class="type">{{neew.category}}.{{neew.ptime}}</div>
            </div>
            </a>
        </div>
    </div>
</template>

<script>
    export default {
         props:[],
         data:function(){
            return{news:[],nnews:[]};
        },
        methods:{
            getData(){
                this.axios.get('https://www.apiopen.top/journalismApi')
                .then((response)=>{this.news=response.data.data.tech;})
                this.axios.get('https://www.apiopen.top/journalismApi')
                .then((response)=>{this.nnews=response.data.data.toutiao;})
               
            }
        },
        mounted(){
            this.getData();
        }

    }
</script>
<style scoped>
   .bookbigbox{
       position: relative;
       padding-top: 4vh;
       padding-bottom: 15vh;
       margin-top: 0px;
       width: 100vw;
       height: 93vh;
       box-sizing: border-box;
       overflow-x: scroll;
   }
   .bookbigbox .box1{
       position: relative;
       overflow: hidden;
       box-sizing: border-box;
       width: 100vw;
       height: 20vh;
       border-bottom: 1px solid rgba(100,100,100,.3);
   }
    .bookbigbox .box1 .left{
        position: relative;
        overflow: hidden;
        margin-left: 5vw;
        margin-top: 3vh;
        float: left;
        width: 35vw;
        height: 25vw;
        box-shadow: 1px 1px 1px 1px rgba(100,100,100,.5);
    }
    .bookbigbox .box1 .left img{
        width: 100%;
        height: 100%;
    }
    .bookbigbox .box1 .right{
        position: relative;
        overflow: hidden;
        margin-right: 5vw;
        margin-top: 3vh;
        float: right;
        width: 50vw;
        height: 24vh;
    }
    .bookbigbox .box1 .right .title{
        position: relative;
        width:100%;
        height: 3vh;
        font-size: 2vh;
        font-weight: 600;
        text-align: left;
        color: rgb(100,100,100);
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
    }
    .bookbigbox .box1 .right .author{
        position: relative;
        margin-top: .5vh;
        padding-left:2vw;
        width:100%;
        height: 3vh;
        font-size: 10px;
        font-weight: 700;
        text-align: left;
        color: rgb(100,100,100);
    }
    .bookbigbox .box1 .right .conent{
        position: relative;
        margin-left: 1vw;
        margin-top:.4vh;
        font-size: 1.5vh;
        width: 48vw;
        height: 5vh;
        text-indent:2em;
        text-align: justify;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
    }
    .bookbigbox .box1 .right .type{
        position: relative;
        margin-top: .4vh;
        padding-left:1.5vw;
        width:100%;
        height: 3vh;
        font-size: 10px;
        font-weight: 700;
        text-align: left;
        color: rgb(100,100,100);
    }
</style>